import React from 'react';
import {v1} from "uuid";

// VARIABLES
const FOLLLOW = 'FOLLLOW'
const UNFOLLLOW = 'UNFOLLLOW'
const SET_USERS = 'SET_USERS'

// TYPES
export type userType = { id: string, name: string, photos: string, isFollowed: boolean, status: string, location: { city: string, country: string } }
export type initialStateType = {
    users: Array<userType>
    pageSize: number,
    totalUsersCount: number,
    currentPage: number
}

// INITIAL STATE
export let initialState: initialStateType = {
    users: [],
    pageSize: 3,
    totalUsersCount: 19,
    currentPage: 1
}


// REDUCER
const usersReducer = (state = initialState, action: any) => {
    switch (action.type) {
        case FOLLLOW:
            return {
                ...initialState,
                users: initialState.users.map(u => {
                    if (u.id === action.userID) {
                        return {...u, isFollowed: true}
                    }
                    return u
                })
            }
        case UNFOLLLOW:
            return {
                ...initialState,
                users: initialState.users.map(u => {
                    if (u.id === action.userID) {
                        return {...u, isFollowed: false}
                    }
                    return u
                })
            }
        case SET_USERS:
            return {
                ...initialState,
                users: [...initialState.users, ...action.usersFromOutside]
            }
        default:
            return initialState
    }
}

// ACTION CREATORS
export const followAC = (userID: string) => ({type: FOLLLOW, userID})
export const unfollowAC = (userID: string) => ({type: UNFOLLLOW, userID})
export const setUsersAC = (usersFromOutside: Array<userType>) => ({type: SET_USERS, usersFromOutside})

export default usersReducer
